      &lt;/mku-options-group&gt;
    &lt;/mku-select&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>Select Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">Select绑定的值，可使用<code>v-model</code>双向绑定</td>
<td style="text-align:left">单选时：<code>Object | Number</code>， 多选时：<code>Array</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">multiple</td>
<td style="text-align:left">支持多选</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">placeholder</td>
<td style="text-align:left">选择框默认的占位符内容</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>请选择</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">placement</td>
<td style="text-align:left">下拉菜单出现的位置</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left"><code>top | top-start | top-end | right | right-start | right-end | bottom | bottom-start | bottom-end | left | left-start | left-end</code></td>
<td style="text-align:left"><code>bottom-start</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">禁用当前Select</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">clearable</td>
<td style="text-align:left">是否可清除，仅单选时有效</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<h3>Select Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">选中项发生变化时触发</td>
<td style="text-align:left">选中项的值</td>
</tr>
<tr>
<td style="text-align:left">clear</td>
<td style="text-align:left">清空时触发</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">drop-change</td>
<td style="text-align:left">dropdown展开收起时触发</td>
<td style="text-align:left">dropdown的状态</td>
</tr>
</tbody>
</table>
<h3>Options Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">options的值</td>
<td style="text-align:left"><code>String | Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">是</td>
</tr>
<tr>
<td style="text-align:left">label</td>
<td style="text-align:left">Opitons显示的内容优先级：slot &gt; label &gt; value; Select显示的文本内容优先级：label &gt; slot读取的textContent文本</td>
<td style="text-align:left"><code>String | Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">禁用当前选项</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
